/* Aplicar la fuente Century Gothic */
@import url('https://fonts.googleapis.com/css2?family=Century+Gothic&display=swap');

body {
  margin: 0;
  font-family: 'Century Gothic', sans-serif;
  background-color: #f4f4f5; /* Color de fondo profesional */
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.app-container {
  text-align: center;
}

/* Estilos para el logo */
.logo-container {
  margin-top: 180px;
}

.logo {
  width: 150px;
  transition: transform 0.3s ease, filter 0.3s ease; /* Movimiento y efecto */
}

.logo:hover {
  transform: rotate(15deg) scale(1.1); /* Gira y aumenta el tamaño */
  filter: brightness(120%); /* Aumenta el brillo */
  cursor: pointer; /* Cambia el cursor */
}

/* Título principal */
.title {
  font-size: 4.5rem;
  color: #333;
  margin-top: 20px;
  font-weight: bold;
  transition: color 0.3s ease, transform 0.3s ease;
  cursor: pointer;
}

.title:hover {
  color: #00ABE4; /* Cambia a azul al pasar el cursor */
  transform: scale(1.1); /* Aumenta ligeramente el tamaño */
}

/* Subtítulo */
.Subtittle {
  font-size: 1.8rem;
  color: #555;
  margin-top: 10px;
}

/* Contenedor de opciones de acceso */
.access-options {
  margin-top: 30px;
}

.access-options h2 {
  font-size: 1.8rem;
  color: #777;
}

.access-button {
  font-size: 1.2rem;
  padding: 10px 20px;
  margin: 10px;
  color: white;
  background-color: #00ABE4; /* Azul profesional */
  border: none;
  border-radius: 5px;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

.access-button:hover {
  background-color: #000075; /* Azul oscuro al pasar el cursor */
  transform: scale(1.1); /* Aumenta ligeramente el tamaño */
}
